import React from 'react'
import textWrap from 'svg-text-wrap'

function TextBox ({ text = '', x = 0, y = 0, maxLines = null, width = 200, fontSize, lineSpacing = 20 }) {
  let lines = textWrap(text, width, { 'font-size': fontSize })
  if (maxLines) {
    lines = lines.slice(0, maxLines)
  }

  return (
    <g>
      {lines.map((line, i) => <text x={x} y={y + i * lineSpacing} key={line + i} fontSize={fontSize}>{line}</text>)}
    </g>
  )
}
export default TextBox
