import React from 'react'
import { dateToString, translate } from '../../lib/utils'

function Titles ({ width, title, subTitle, showDate, startDate, endDate }) {
  return (
    <g transform={translate(0, 130)}>
      <text x={width / 2} y='0' fill='#000' fontSize='30' textAnchor='middle' alignmentBaseline='hanging'>{title}</text>
      <text x={width / 2} y='40' fill='#666' fontSize='15' textAnchor='middle' alignmentBaseline='hanging'>{subTitle}</text>
      {showDate && <text x={width / 2} y='64' fill='#666' fontSize='8' textAnchor='middle' alignmentBaseline='hanging'>{dateToString(startDate)} - {dateToString(endDate)}</text>}
    </g>
  )
}
export default Titles
