import React from 'react'
import { dateToString, translate, pad } from '../../lib/utils'

const getMonthName = (date) => {
  return date.toLocaleString('default', { month: 'short' })
}

function DateAxis ({ startStamp, endStamp, chartW, chartX, y, fontSize = 7 }) {
  const year = new Date(0)
  year.setFullYear(year.getFullYear() + 1)
  const yearMs = year.getTime()
  const dayMs = 1000 * 60 * 60 * 24
  const diff = (endStamp - startStamp) * 1000
  const times = []
  const startDate = new Date(startStamp * 1000)

  if (diff > 2 * yearMs) { // over 2 years
    const years = Math.floor(diff / yearMs)
    startDate.setMonth(0)
    startDate.setDate(1)

    for (let i = 0; i <= years; i++) {
      times.push({ title: startDate.getFullYear(), timeStamp: startDate.getTime() })
      startDate.setFullYear(startDate.getFullYear() + 1)
    }
  } else if (diff > yearMs) { // 2 years - 1 years
    console.log('over 1 year')
    const months = Math.floor(diff / (yearMs / 12))
    console.log(startDate)
    if (startDate.getDate() > 16) { // half of the month has eleapsed no need to print title
      startDate.setMonth(startDate.getMonth() + 1)
    }
    startDate.setDate(1)
    console.log(startDate)

    for (let i = 0; i <= months; i++) {
      times.push({ title: startDate.getFullYear() + ' ' + getMonthName(startDate), timeStamp: startDate.getTime() })
      startDate.setMonth(startDate.getMonth() + 1)
    }
  } else if (diff > 3 * yearMs / 12) { // 1 year - 3 months
    console.log('over 3 months')
    const months = Math.floor(diff / (yearMs / 12))
    if (startDate.getDate() > 16) { // half of the month has eleapsed no need to print title
      startDate.setMonth(startDate.getMonth() + 1)
    }
    startDate.setDate(1)

    for (let i = 0; i <= months; i++) {
      times.push({ title: getMonthName(startDate), timeStamp: startDate.getTime() })
      startDate.setMonth(startDate.getMonth() + 1)
    }
  } else if (diff > dayMs * 30) { // 3 months - 30 days
    console.log('over 30 days')
    const days = Math.floor((diff / dayMs) / 2) // adding 2 days at the time
    startDate.setHours(1)

    for (let i = 0; i <= days; i++) {
      times.push({ title: dateToString(startDate), timeStamp: startDate.getTime() })
      startDate.setDate(startDate.getDate() + 2)
    }
  } else if (diff > dayMs * 1) { // 30 days - 3 days
    console.log('over 3 days')
    const days = Math.floor(diff / dayMs)
    startDate.setHours(1)

    for (let i = 0; i <= days; i++) {
      times.push({ title: dateToString(startDate), timeStamp: startDate.getTime() })
      startDate.setDate(startDate.getDate() + 1)
    }
  } else { // 1 days - 0
    console.log('print hours...')
    const hours = Math.floor(diff / (1000 * 60 * 60))
    startDate.setMinutes(0)
    startDate.setSeconds(0)

    for (let i = 0; i <= hours; i++) {
      times.push({ title: pad(startDate.getHours()) + ':' + pad(startDate.getMinutes()), timeStamp: startDate.getTime() })
      startDate.setHours(startDate.getHours() + 1)
    }
  }

  return (
    <g transform={translate(chartX, y)}>
      {/* <rect width={chartW} height={10} fill='red' /> */}
      {times.map((dateObj) => <text textAnchor='middle' fontSize={fontSize} fill='#888' x={chartW * ((dateObj.timeStamp - startStamp * 1000) / diff)} key={'graphDate' + dateObj.timeStamp}>{dateObj.title}</text>)}
    </g>
  )
}
export default DateAxis
